.container {
  display: flex;
  flex-direction: column;
  max-width: 28%;
  height: 22rem;
  border: 3px solid black;
  margin: 20px 20px;
  flex-grow: 1;
  flex-shrink: 1;
}

.imageExercise {
  width: 100%;
  max-height: 50%;
  border-bottom: 2px solid black;
}

.titleContainer {
  padding: 0.25rem;
  background-color: #212121;
  color: white;
}

.content {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
}

.description {
  padding: 0.18rem;
  max-height: 5rem;
  flex: 1 0 7rem;
  overflow: hidden;
}

.buttonExercise {
  margin: 0.25rem 0.25rem 0 0.25rem;
  float: right;
  width: 50%;
  height: 2.5rem;
  background-color: black;
  color: white;
  border-radius: 20px;
  font-size: 16px;
  font-weight: bold;
}

.buttonExercise:hover {
  background-color: rgb(77, 77, 77);
}
