.container {
  height: 10%;
  max-height: 10%;
  width: 90%;
  border: 3px solid black;
  margin: 15px 0;
}

.titleContainer {
  background-color: #212121;
  color: white;
  text-align: center;
  padding: 5px 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: hidden;
}

.titleContainer h3{
  padding: 0 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.contentContainer {
  float: left;
  padding: 0.5rem;
}

.contentContainer p{
  display:block;
  overflow: hidden;
  max-height: 100px;
  line-height: 20px;;
}

.buttonNews {
  width: 6rem;
  height: 2.5rem;
  background-color: black;
  color: white;
  border-radius: 20px;
  font-size: 16px;
  font-weight: bold;
  /* margin: 0 5px 5px 0; */
  float: right;
}

.buttonNews:hover {
  background-color: rgb(77, 77, 77);
}
