.container {
  max-width: 28%;
  height: 24rem;
  border: 3px solid black;
  margin: 15px 10px;
}

.imageRecipe {
  width: 100%;
  height: auto;
  max-height: 55%;
  border-bottom: 2px solid black;
}

.titleContainer {
  margin: 3px;
}

.buttonContainer {
  margin-top: 1rem;
  text-align: center;
}

.buttonRecipe {
  width: 50%;
  height: 2.5rem;
  background-color: black;
  color: white;
  border-radius: 20px;
  font-size: 16px;
  font-weight: bold;
}

.buttonRecipe:hover {
  background-color: rgb(77, 77, 77);
}
